<template>
    <div><p class="headline primary--text">Редактировать новость</p>

        <form class="mt-3">
            <v-text-field
                    v-model="newsDto.title"
                    label="Заголовок"
                    :error-messages="errors.collect('title')"
                    v-validate="'required'"
                    data-vv-name="title"
                    required>

            </v-text-field>

            <v-text-field
                    v-model="newsDto.newsText"
                    label="Текст"
                    v-validate="'required'"
                    multi-line
                    rows="1"
                    :error-messages="errors.collect('newsText')"
                    auto-grow
                    data-vv-name="newsText"
                    required>
            </v-text-field>

            <v-flex xs12 sm6>
                <v-select
                        :items="years"
                        v-model="newsDto.year"
                        label="Год обучения"
                        single-line
                        clearable
                        autocomplete
                        prepend-icon="grade"
                ></v-select>
            </v-flex>
            <v-menu
                    ref="deadline"
                    lazy
                    :close-on-content-click="false"
                    v-model="deadline"
                    transition="scale-transition"
                    offset-y
                    full-width
                    :nudge-right="40"
                    min-width="290px"
                    :return-value.sync="newsDto.deadline">
                <v-text-field
                        v-model="newsDto.deadline"
                        slot="activator"
                        :error-messages="errors.collect('date')"
                        label="Дедлайн"
                        v-validate="'required'"
                        prepend-icon="event"
                        readonly>
                </v-text-field>

                <v-date-picker v-model="newsDto.deadline"
                               @input="$refs.deadline.save(newsDto.deadline)"></v-date-picker>

            </v-menu>


            <v-btn class="mt-5 right" color="primary" @click="submit">submit</v-btn>
        </form>
    </div>
</template>

<script>
    import axios from 'axios'

    export default {
        data: () => ({
            newsDto: {
                newsText: 'text',
                title: 'title',
                deadline: '2020-04-14',
                year: 1,
                id:null
            },
            years: [1, 2, 3, 4],
            deadline: false,

            valid: true,
            requiredRules: [
                v => !!v || 'Это поле обязательно для заполнения'
            ],
            dictionary: {
                custom: {
                    title: {
                        required: () => 'Введите заголовок',
                    },
                    newsText: {
                        required: () => 'Введите текст',
                    },
                    date: {
                        required: () => 'Выберите дату дедлайна',
                    }, deadline: {
                        required: () => 'Выберите дату дедлайна',
                    }
                }
            }
        }),
        mounted() {
            this.$validator.localize('ru', this.dictionary)
        },
        methods: {
            submit() {
                if (this.$validator.validateAll()) {
                    let headers = {
                        'Content-Type': 'application/json',
                        'Accept': 'application/json'
                    };
                    axios.post(`/news/api/add`, this.newsDto, headers)
                        .then(response => {
                            this.newsDto = response.data;
                            if (this.newsDto.id != null) {
                                this.$router.push('/news');
                            }
                        })
                        .catch(e => {
                            this.errors.push(e)

                        })
                }
            }
        }, created() {
            axios.get(`/news/api/single/${ this.$route.params.id}`)
                .then(response => {
                    this.newsDto = response.data
                })
                .catch(e => {
                    this.errors.push(e)
                })
        }
    }
</script>

<codepen-resources lang="json">
    {
    "js": ["https://unpkg.com/vee-validate@latest/dist/vee-validate.js"]
    }
</codepen-resources>



