<template>
    <div id="news">

        <v-btn color="blue darken-3" @click="getAllNews(currentPage)" class="ml-3 " dark>Обновить</v-btn>


        <div class="mt-3 mb-3 ml-3 mb-5">


            <!--News list-->
            <v-card>
                <v-list three-line v-for="(item, index) in news" :key="index">
                    <template>
                        <v-flex xs12 sm3 class="right">
                            <v-btn @click="restoreNews(item.id)" color="primary">
                                Востановить
                            </v-btn>
                        </v-flex>
                        <v-list-tile>

                            <v-list-tile-avatar class="mt-0">
                                <v-avatar color="blue darken-4" size="25">
                                    <span class="white--text subheader">{{item.year}} </span>
                                </v-avatar>
                            </v-list-tile-avatar>

                            <v-list-tile-content class="mt-0">
                                <v-list-tile-title v-html="item.title"></v-list-tile-title>
                                <v-list-tile-sub-title v-html="item.newsText"></v-list-tile-sub-title>
                                <v-list-tile-sub-title>Дедлайн: {{item.deadline}}</v-list-tile-sub-title>
                                <v-list-tile-sub-title>Дата удаления: {{item.deleteDate}}</v-list-tile-sub-title>
                            </v-list-tile-content>

                        </v-list-tile>

                        <v-divider class="mt-0"/>
                    </template>
                </v-list>


                <v-layout class="right mt-3" row>
                    <v-btn @click="previousPage" icon color="primary">
                        <v-icon>navigate_before</v-icon>
                    </v-btn>

                    <h1 class="mt-0 ml-3 mr-3">{{currentPage + 1}}</h1>
                    <v-btn @click="nextPage" icon color="primary">
                        <v-icon>navigate_next</v-icon>
                    </v-btn>
                </v-layout>


            </v-card>
        </div>
    </div>
</template>

<script>
    import Icon from "vue-awesome/components/Icon";
    import axios from 'axios';
    import VPaginator from "vuejs-paginator-axios/src/VPaginator";
    import AddNews from "./AddNews";

    export default {
        components: {AddNews, VPaginator, Icon},
        data() {
            return {
                news: [
                    {
                        id: 1,
                        newsText: 'tgest',
                        title: "titgggggggggffffftitgggggggggffffftitgggggggggffff" +
                        "ftitgggggggggffffftitgggggggggffffftitgggggggggffffftitggg" +
                        "ggggggffffftitgggggggggffffftitgggggggggffffftitgggggggggfffffti" +
                        "tgggggggggffffftitgggggggggffffftitgggggggggffffftitgggggggggfffff" +
                        "titgggggggggfffff",
                        course: {},
                        year: 3,
                        deadline: 454685412,
                        deleteDate: 45
                    },
                    {
                        id: 2,
                        newsText: 'testtesttesttesttesttesttest' +
                        'testtesttesttesttesttesttesttesttesttesttesttest' +
                        'testtesttesttesttesttesttesttest' +
                        'testtesttesttesttesttesttesttesttesttesttesttesttest' +
                        'testtest',
                        title: "title",
                        course: {},
                        year: 2,
                        deadline: 454685412,
                        deleteDate: 45
                    },
                    {
                        id: 3,
                        newsText: 'test',
                        title: "title",
                        course: {},
                        year: 1,
                        deadline: 454685412,
                        deleteDate: 45
                    }
                ],

                currentPage: 0,
            }

            // return {
            //     news: [],
            // }

        },
        created() {
            this.refreshNews()
        },
        methods: {
            getAllNews(page) {
                axios.get(`/news/api/deleted/${page}`)
                    .then(response => {
                        this.news = response.data;
                        this.removeFilters = true;
                    })
                    .catch(e => {
                        this.errors.push(e)
                    })
            },
            refreshNews() {
                this.currentPage = 0;
                this.getAllNews(this.currentPage);
            },
            previousPage() {
                if (this.currentPage != 0) {
                    this.currentPage--;
                    this.getAllNews(this.currentPage);
                }
            },
            nextPage() {
                this.currentPage++;
                this.getAllNews(this.currentPage);
            },
            restoreNews(id) {

            }

        }
    }

</script>
<style>

</style>




